@font-face {
    font-family: 'Minecraftia';
    src: url('../fonts/Minecraftia-Regular.ttf');
}

body {
    background-color: $color-1;
    background-image: $wallpaper;
    background-size: 100% cover no-repeat;
    color: white;
    font-size: 110%;
}

// Windows Taskbar
.footer {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 50px;
    backdrop-filter: blur(15px);
    padding: 5px;
    text-align: center;
    z-index: 1000;

    &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        opacity: .6;
        background-color: #fff;
        background-image: url("../img/acrylic.png");
    }

    img {
        display: inline-block;
        position: relative;
        padding: 5px;
        vertical-align: top;
        border-radius: 4px;
        height: 35px;
        transition: background-color .2s linear;


        &.focused,
        &:hover {
            background-color: rgba(255, 255, 255, .4);
        }

    }
}

.center {

    padding: 30px;
    margin: 0 auto;
    width: 75%;
}

.panel {
    border-radius: 5px;
    width: 75%;
    height: 85%;
    backdrop-filter: blur(15px);
    box-shadow: 0 1.6px 3.6px 0 rgb(0 0 0 / 13%), 0 0.3px 0.9px 0 rgb(0 0 0 / 11%);
    position: fixed;

    &:after {
        margin: 0 auto;
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        opacity: .4;
        background-color: #fff;
        border-radius: 5px;
        background-image: url("../img/acrylic.png");
    }
}

.note-panel {
    max-height: 100%;
}

.panel-body {
    max-height: 92%;
    overflow-y: scroll;
}

.panel-body::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0);
}

.panel-body::-webkit-scrollbar {
    width: 5px;
    background-color: rgba(0, 0, 0, 0);
}

.panel-body::-webkit-scrollbar-thumb {
    background-color: rgb(1 2 3 / 20%);
}

.noteBodyText {
    padding: 20px 60px;
    color: #222222;
}

// Window titlebar
.header {
    color: #5e6469;
    height: 33px;

    >.headerIcons {
        position: absolute;
        color: #222222;
        right: 10px;
        display: inline-block;
        text-align: right;
        font-size: .8rem;

        >i {
            padding: 5px;
            cursor: pointer;
            transition: all .2s linear;
            padding: 10px;

            &:hover {
                color: #222222;
                background-color: rgba(250, 250, 250, .4);
            }
        }

        >a {
            padding: 5px;
            cursor: pointer;
            transition: all .2s linear;
            padding: 10px;
            color: #222222;

            &:hover {
                color: #222222;
                background-color: rgba(250, 250, 250, .4);
            }
        }
    }
}


.headerTitleText {
    font-size: 1rem;
    position: absolute;
    color: #123fbc;
    margin-top: 0;
    margin-left: 20px;
    padding: 10px;
}

.notepadIcon {
    height: 20px;
}

.headerImageOverlay {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2.5rem;
    font-weight: 400;
    text-shadow: 0px 0px 10px #000;
    background-color: rgba(4, 27, 104, .1);
    width: 100%;
    height: 100%;
}

.headerImage {
    background-image: url('../img/wallpaper.webp');
    width: 100%;
    height: 350px;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    background-position-y: -75px;

}

// Windows 11 Buttons


button {
    width: 100%;
    padding: 0.7rem;
    margin-top: 10px;
    border: none;
    background: #0067c0;
    font-family: "Segoe UI", sans-serif;
    border-radius: 5px;
    color: white;
    transition: all 350ms;
}


button:hover {
    background: #02549c;
}

button:active {
    transform: scale(0.99, 0.99);
    padding: 0;
}


.input {
    padding: 0.6rem 1.7rem;
    padding-bottom: 0.8rem;
    background: rgb(255, 255, 255);
    border-radius: 5px;
    width: 100%;
    position: relative;
    overflow: hidden;
    box-shadow: 0 0 1px 1px #e3e3e3;
}

.input input {
    background: none;
    border: none;
    font-family: "Segoe UI", sans-serif;
    outline: none;
    font-size: 0.9em;
    box-sizing: border-box;
    width: 100%;
}

.input span {
    width: 130%;
    height: 3px;
    position: absolute;
    left: -10%;
    top: 95%;
    background: #868786;
    transition: background 100ms;
}

.input input:focus~span {
    background: #0067c0;
}